@import url("https://fonts.googleapis.com/css?family=Roboto");

//@font-face {font-family: "Roboto";}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 20px;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: inherit;
}

.clearfix:after {
  content: "";
  display: block;
  width: 100%;
  clear: both;
}

html {
  overflow-x: hidden;
  width: 100%;
}

body {
  font-family: "Roboto";
  overflow-x: hidden;
  width: 100%;
  padding: 25px;
  line-height: 1.5;
  color: #686868;
}

.container {
  width: 100%;
  margin: 0 auto;
  max-width: 1200px;
}

.text-center,
.social-wrap,
h2,
h3,
.nav-main {
  text-align: center;
}

.arrow {
  transition: all 0.5s;
}
.arrow:hover {
  transform: scale(2.5);
}

/* MODULES - Individual style components */
/* buttons */
button {
  background-color: #d07c7c;
  color: #ffffff;
  border: none;
  cursor: pointer;
  padding: 10px 30px;
  text-transform: uppercase;
  font-size: 12px;
  font-size: 0.75rem;
  transition: opacity 0.5s;
}
button:hover {
  opacity: 0.6;
}

.button-send {
  margin-top: 10px;
}

/* form */
form {
  margin: 20px auto;
  width: 100%;
  max-width: 550px;
}
form p {
  font-size: 12px;
  font-size: 0.75rem;
}

input,
textarea {
  display: block;
  width: 100%;
  font-size: 12px;
  font-size: 0.75rem;
  outline: none;
  padding: 15px;
  color: #686868;
  border: 2px solid #d07c7c;
  margin-bottom: 20px;
}

textarea {
  resize: none;
}

section {
  padding: 50px 0;
}
section .text-center p,
section h2 p,
section h3 p,
section .nav-main p {
  padding: 0 20px;
  max-width: 900px;
  margin: 0 auto;
}

/* work */
.work-wrap:before,
.work-wrap:after {
  content: " ";
  display: table;
}

.work-wrap:after {
  clear: both;
}

figure {
  position: relative;
  margin: 10px 0;
  cursor: pointer;
  display: inline-block;
  width: 100%;
}
figure:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #000;
  opacity: 0;
  transition: all 1s ease-in-out;
}
figure:hover:before {
  opacity: 0.7;
}
figure:hover .caption-head:before {
  left: -20px;
  top: -20px;
}
figure:hover .caption-head:after {
  right: -20px;
  top: -20px;
}
figure:hover .caption-blurb:before {
  left: -20px;
  bottom: -20px;
}
figure:hover .caption-blurb:after {
  right: -20px;
  bottom: -20px;
}
figure img {
  width: 100%;
  display: block;
}
figure figcaption {
  position: absolute;
  top: 50%;
  left: 50%;
  min-width: 200px;
  transform: translate(-50%, -50%);
  color: #ffffff;
  line-height: 2;
  transition: all 1s ease-in-out;
}
figure figcaption:before {
  content: "";
  position: absolute;
  width: 80%;
  height: 2px;
  top: calc(50% + 2px);
  transform: translate(-50%, -50%);
  background-color: #ffffff;
}
figure figcaption .caption-head {
  position: relative;
  font-family: "Roboto";
  text-transform: uppercase;
  font-size: 22px;
  font-size: 1.375rem;
}
figure figcaption .caption-head:before {
  content: "";
  position: absolute;
  border-top: 2px solid #ffffff;
  border-left: 2px solid #ffffff;
  height: 20px;
  width: 20px;
  left: 0;
  top: 0;
  transition: all 1s ease-in-out;
}
figure figcaption .caption-head:after {
  content: "";
  position: absolute;
  border-top: 2px solid #ffffff;
  border-right: 2px solid #ffffff;
  height: 20px;
  width: 20px;
  right: 0;
  top: 0;
  transition: all 1s ease-in-out;
}
figure figcaption .caption-blurb {
  position: relative;
}
figure figcaption .caption-blurb:before {
  content: "";
  position: absolute;
  border-bottom: 2px solid #ffffff;
  border-left: 2px solid #ffffff;
  height: 20px;
  width: 20px;
  left: 0;
  bottom: 0;
  transition: all 1s ease-in-out;
}
figure figcaption .caption-blurb:after {
  content: "";
  position: absolute;
  border-bottom: 2px solid #ffffff;
  border-right: 2px solid #ffffff;
  height: 20px;
  width: 20px;
  right: 0;
  bottom: 0;
  transition: all 1s ease-in-out;
}

#testimonials {
  padding: 5px 0;
}

/* video*/
.video {
  height: 500px;
  background: url("https://i.imgur.com/0IRYG66.jpg") no-repeat;
  background-size: cover;
  position: relative;
}

/*contact */
#contact {
  position: relative;
}
#contact:before {
  content: "";
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url("https://image.ibb.co/h9qYHA/bg-video.png") no-repeat center
    center;
  opacity: 0.1;
  z-index: -1;
}

h2,
h3 {
  position: relative;
  font-family: "Roboto";
  text-transform: uppercase;
  padding-bottom: 14px;
  margin-bottom: 30px;
  font-size: 22px;
  font-size: 1.375rem;
}
h2:after,
h3:after {
  content: "";
  position: absolute;
  display: block;
  width: 72px;
  height: 3px;
  left: calc(50% - 36px);
  bottom: 0px;
  background: #d07c7c;
}

h3 {
  text-transform: none;
}

/* LAYOUT  */
header {
  position: relative;
  background: url("https://image.ibb.co/jBXfxA/bg-contact.jpg") no-repeat center
    center/cover;
  color: #ffffff;
  padding: 15% 0 0 0;
  min-height: 650px;
}

.title-main,
h1 {
  line-height: 1.2;
  font-size: 30px;
  font-size: 1.875rem;
}
.title-main span,
h1 span {
  display: block;
}
.title-main span:last-child,
h1 span:last-child {
  font-size: 23px;
  font-size: 1.4375rem;
}

.title-sub {
  font-size: 48px;
  font-size: 3rem;
  position: relative;
}

.title-sub:after {
  content: "";
  position: absolute;
  display: block;
  width: 82px;
  height: 3px;
  left: calc(50% - 41px);
  bottom: -10px;
  background: #ffffff;
}

.title-blurb {
  padding: 40px 0;
}
.title-blurb span {
  display: block;
  line-height: 25px;
}

.menu {
  color: #686868;
  background: #ffffff;
  display: block;
  position: absolute;
  right: 0;
  bottom: 0;
  width: 100%;
  line-height: 40px;
  //vertical-align: middle;
  padding: 10px;
  border-bottom: 2px solid rgba(104, 104, 104, 0.1);
}

h1 {
  font-size: 22px;
  font-size: 1.375rem;
  display: inline-block;
}

.nav-toggle {
  display: inline-block;
  //float: right;
  width: 40px;
  height: 40px;
  cursor: pointer;
  transition: all 0.5s ease;
}
.nav-toggle div {
  display: block;
  width: 40px;
  height: 3px;
  margin-top: 7px;
  background-color: #686868;
  transition: all 0.5s;
}
.nav-toggle:hover {
  opacity: 0.7;
}

.nav-main {
  display: none;
  clear: both;
  background: #ffffff;
}
.nav-main a {
  position: relative;
  text-decoration: none;
  padding-bottom: 2px;
  transition: 0.3s;
  display: block;
  width: 100%;
  padding: 20px 0;
}
.nav-main a::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  height: 4px;
  width: 100%;
  background-color: #d07c7c;
  transform: scale(0, 1);
  transform-origin: 0 50%;
  transition: 0.3s;
}
.nav-main a:hover {
  color: #d07c7c;
  font-weight: bold;
}
.nav-main a:hover::before {
  transform: scale(1, 1);
}

footer .copy {
  margin-top: 20px;
}

@media (min-width: 500px) {
  .caption-blurb {
    font-size: 18px;
    font-size: 1.125rem;
  }
}

@media (min-width: 768px) {
  header {
    min-height: 95vh;
    padding-top: 14em;
  }
  .figure-sm {
    width: 48%;
    display: block;
    float: left;
  }
  .figure-sm.one {
    margin-left: 4%;
  }
}

@media (min-width: 992px) {
  .nav-toggle {
    display: none;
  }
  .nav-main {
    display: block;
    float: right;
    width: 70%;
  }
  .nav-main ul {
    text-align: right;
  }
  .nav-main li {
    display: inline-block;
    margin-left: 40px;
  }
  .nav-main a {
    text-transform: uppercase;
    padding: 0;
  }
  .work-column-lg {
    display: block;
    width: 61.5%;
    float: left;
  }
  .work-column-sm {
    display: block;
    width: 35.5%;
    float: left;
    margin-left: 3%;
  }
  .work-column-sm img {
    height: auto;
  }
  form {
    padding-bottom: 40px;
  }
  //float:left;
  form p {
    display: inline-block;
  }
  form .button-send {
    float: right;
    margin-top: 0;
  }
  .text-center form p,
  h2 form p,
  h3 form p,
  .nav-main form p {
    margin-top: 10px;
  }
}
